<template>
  <div >

    <el-card shadow="hover" style="height:600px;width:390px" class="card" >
      <div  class="clearfix">
          <span>个人信息</span>
          <el-button type="text"  icon="el-icon-edit" style='float: right' @click="handleEdit()">编辑</el-button>
      </div>
      <el-row>
        <div class="infoText">学号:<span  style='margin-left: 30px'>{{myInfo.stuNum}}</span></div>
      </el-row>
      <el-divider></el-divider>
      <el-row>
        <div class="infoText">姓名:<span style='margin-left: 30px'>{{myInfo.stuName}}</span></div>
      </el-row>
      <el-divider></el-divider>
      <el-row>
        <div class="infoText">班级:<span style='margin-left: 30px'>{{myInfo.stuclass}}</span></div>
      </el-row>
      <el-divider></el-divider>
      <el-row>
        <div class="infoText">邮箱:<span style='margin-left: 30px'>{{myInfo.stuemail}}</span></div>
      </el-row>
      <el-divider></el-divider>
      <el-row>
        <div class="infoText">电话:<span style='margin-left: 30px'>{{myInfo.stuphone}}</span></div>
      </el-row>
      <el-divider></el-divider>
      <el-row>
        <div class="infoText">住址:<span style='margin-left: 30px'>{{myInfo.stuaddress}}</span></div>
      </el-row>
    </el-card>

    <el-dialog title="编辑" v-model="editVisible" width="30%">
      <el-form  label-width="70px">
        <el-form-item label="学号">
            <el-input type='text' v-model="edit.stuNum" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="姓名">
            <el-input type='text' v-model="edit.stuName"></el-input>
        </el-form-item>
        <el-form-item label="班级">
            <el-input type='text' v-model="edit.stuclass"></el-input>
        </el-form-item>
        <el-form-item label="邮箱">
            <el-input type='text' v-model="edit.stuemail" ></el-input>
        </el-form-item>
        <el-form-item label="电话">
            <el-input type='text' v-model="edit.stuphone"></el-input>
        </el-form-item>
        <el-form-item label="地址">
        <el-input type='text' v-model="edit.stuaddress"></el-input>
        </el-form-item>
      </el-form>
      <span  class="dialog-footer">
        <el-button @click="editVisible = false;this.doQuery();">取 消</el-button>
        <el-button type="primary" @click="saveEdit()">确 定</el-button>
      </span>
    </el-dialog>
  </div>

  <div class="demo-image__preview">
    <el-carousel height="600px" >
    <el-image
      style="width: 1000px; height: 600px"
      :src="require('@/assets/2.jpg')"
    >
    </el-image>

    </el-carousel>
  </div>
</template>

<script>
import  '@/views/Login'
import { ElMessage } from 'element-plus'
import { userLoginReqid } from '@/service/userServ.js'
import { studentSubmit } from '@/service/genServ.js'
import { getstudentInfo} from "@/service/genServ.js";
export default {
  name: "studentpage",
  data() {
    return {
      
      myInfo: {
        stuNum:'',
        stuName:'',  
        stuclass:'',  
        stuemail:'',
        stuphone:'',
        stuaddress:'',
    },
    edit: {
        stuNum:'',
        stuName:'',  
        stuclass:'',  
        stuemail:'',
        stuphone:'',
        stuaddress:'',
    },
    editVisible: false,
    passwordForm:{
        oldPassword:"",
        newPassword:'',
        confirmPwd:'',
    },
    passwordVisible:false,
    };
  },
  created() {
    this.doQuery();
  },
  methods: {
    doQuery() {
      userLoginReqid(localStorage.getItem('username'), localStorage.getItem('password') ).then(id=>{
         getstudentInfo({userId:id}).then((res) => {
          this.myInfo = res.data;
        }
      );
      })
    },

    handleEdit() {
      this.edit = this.myInfo;
      this.editVisible = true;
    },

    saveEdit() {
      const emailReg = /^\w{3,}(\.\w+)*@[A-z0-9]+(\.[A-z]{2,5}){1,2}$/;
      if (!emailReg.test(this.edit.stuemail)) {
        ElMessage({
          message: '请输入合法邮箱',
          type: 'error',
        })
      } 
      else if (this.edit.stuphone.length !==11){
        ElMessage({
          message: '请输入合法手机号',
          type: 'error',
        })
      }
      else{
        this.editVisible = false;
        studentSubmit({form:this.edit}).then((res)=>{
          console.log(res);
          ElMessage({
          message: '提交成功！',
          type: 'success',
        })
          this.doQuery();
        }
      )}
    },   
   
  },
}
</script>

<style scoped>
.card{
  position: absolute;
  left: 250px;
}
.infoText {
    font-size: 20px;
    font-weight: bold;
    margin-left: 24px;
    margin-top: 14px;
    color: #1f2f3d;
}
.demo-image__preview{
  position: absolute;
  left: 750px;
}
</style>
